.freed{
  display: flex;
  width: 100%;
  height: 300px;
  h1{
    text-transform: uppercase;
    font-size: 32px;
  }
  
  &__wrapper{
    display: flex;
    width: 100%;
    justify-content: space-around;
  }

  &__left{
    width: 50%;
    border-right: 1px solid red;
    background-color: #978858;
    display: flex;
    align-items: center;
  }

  &__right{
    width: 50%;
    border-right: 1px solid red;
    display: flex;
    align-items: center;
  }
}